@import url(https://fonts.googleapis.com/css?family=PT+Sans+Caption&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root{
  --base-1 : #00ced1;
  --base-2 : #00bfff;
  --base-3 : #0070ff;
  --base-4 :	#b9f2ff;
  --base-5 :	#abcdef;
  --base-purple-1: #d323ff;
  --base-purple-2: #da89ff;
}


.Main
{
  text-align: center;
  
  display: flex;
  align-content: center;
  justify-content: center;
  width: 100%;
  height: 100%;

}


body{
  background-color: #00bfff;
  background-color: var(--base-2);
  font-family: 'PT Sans Caption', sans-serif !important;
}


.Main-container
{
  margin: 5% auto;
}

.Main-content
{
  width: 100%;
  margin: 0 auto;
}

.main-title{
  text-transform: capitalize;
  font-size: 40px;
  font-weight: 800;
  padding-bottom: 40px;
}

.main-buttons{
  padding: 50% 0;
}

.main-content-button
{
  display: inline-block;
  padding: 0 10px;
}

.webcam{
  display: flex;
  justify-content: center;
  align-content: center;
}

@-webkit-keyframes gradientFlow
{
  0%{
    background-color: #00bfff;
    background-color: var(--base-2);
  }

  40%{
    background-color: #00ced1;
    background-color: var(--base-1);
  }

  100%{
    background-position: #00bfff;
    background-position: var(--base-2);
  }
}

@keyframes gradientFlow
{
  0%{
    background-color: #00bfff;
    background-color: var(--base-2);
  }

  40%{
    background-color: #00ced1;
    background-color: var(--base-1);
  }

  100%{
    background-position: #00bfff;
    background-position: var(--base-2);
  }
}

@-webkit-keyframes gradientFlowPurple
{
  0%{
    background-color: #d323ff;
    background-color: var(--base-purple-1);
  }

  40%{
    background-color: #da89ff;
    background-color: var(--base-purple-2);
  }

  100%{
    background-position: #d323ff;
    background-position: var(--base-purple-1);
  }
}

@keyframes gradientFlowPurple
{
  0%{
    background-color: #d323ff;
    background-color: var(--base-purple-1);
  }

  40%{
    background-color: #da89ff;
    background-color: var(--base-purple-2);
  }

  100%{
    background-position: #d323ff;
    background-position: var(--base-purple-1);
  }
}

@-webkit-keyframes fadeIn
{
  from{
    opacity: 0;
  }

  to{
    opacity: 1;
  }
}

@keyframes fadeIn
{
  from{
    opacity: 0;
  }

  to{
    opacity: 1;
  }
}


.maincontainer{
  position: absolute;
  width: 35%;
  height: 20%;
  background: none;
  top: 20%;
  left: 49%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);

}

.flashcard{
  font-size: 35pt;
  font-weight: 700pt;
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: all 0.8s ease;
  transition: all 0.8s ease;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.flashcard:hover{
  cursor: pointer;
}

.thefront{
  box-shadow: 5px 5px #88888860;
  padding-top: 20%;


  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  overflow: hidden;
  background: white;
  color: #000;
}

.theback{
  box-shadow: 5px 5px #88888860;
  padding-top: 20%;
  display: flex;
  justify-content: center;
  align-content: center;


  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  overflow: hidden;
  background: #333;
  color: white;
  text-align: center;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.partofspeech{
  font-size: 15pt;
  font-weight: normal;
  display: contents;
  font-style: italic;
  text-transform: capitalize;
}


.flashcard-word{
  font-size: 25pt;
}

.flashcard-def{
  font-size: 15pt;
}

.wordlist{
  box-shadow: 5px 5px #888888d2;
  position: absolute;
  top: 55%;
  left: 18%;
  height: 20%;
  width: 20%;
  background-color: white;
  border-radius: 10px;
}

.word-definition{
  overflow: auto;


  box-shadow: 5px 5px #888888d2;
  position: absolute;
  top: 55%;
  right: 18%;
  height: 20%;
  width: 20%;
  color: rgb(27, 27, 27);
  border-radius: 10px;
  background-color: #d323ff;
  background-color: var(--base-purple-1);
}

.word-def-title{
  padding: 5px 5px;
  font-size: 20pt;
  font-weight: 700;
}

.word-def-text{
  padding: 5px 5px;
  font-size: 10pt;
}

.for-back{
  border-radius: 10px;
  padding-top: 2px;
  display: inline-block;
  margin: 0 10px;
  margin-top: -20px;
}

.for-back:hover{
  cursor: pointer;
  background-color: rgba(0,0,0,0.2);
}

.navbar{
  width: 100%;
  position: fixed;
  bottom: 0;
}
